export const getGameResult = (totalScore: number): GameResult => {
  if (totalScore >= 40) {
    return {
      score: totalScore,
      title: " ",
      description: "",
      advice: "UFO",
      compatibility: 99
    };
  } else if (totalScore >= 35) {
    return {
      score: totalScore,
      title: " ",
      description: "",
      advice: "",
      compatibility: 88
    };
  } else if (totalScore >= 30) {
    return {
      score: totalScore,
      title: " ",
      description: "",
      advice: "''",
      compatibility: 75
    };
  } else if (totalScore >= 25) {
    return {
      score: totalScore,
      title: " ",
      description: "''",
      advice: "''",
      compatibility: 65
    };
  } else {
    return {
      score: totalScore,
      title: " AI",
      description: "AIAI",
      advice: "''",
      compatibility: 50
    };
  }
};