import React, { useEffect, useState } from 'react';
import { useGame } from '../contexts/GameContext';
import { Copy, Share2, QrCode, Clock, Heart, RefreshCw } from 'lucide-react';
import { toast } from 'sonner';

const WaitingPage: React.FC = () => {
  const { inviteCode, checkPartnerStatus, resetGame } = useGame();
  const [copied, setCopied] = useState(false);
  const [isChecking, setIsChecking] = useState(false);
  
  const inviteUrl = `${window.location.origin}${window.location.pathname}?invite=${inviteCode}`;
  
  // 
  useEffect(() => {
    const interval = setInterval(() => {
      checkPartnerStatus();
    }, 3000); // 3
    
    return () => clearInterval(interval);
  }, [checkPartnerStatus]);
  
  const handleCopyLink = async () => {
    try {
      await navigator.clipboard.writeText(inviteUrl);
      setCopied(true);
      toast.success('');
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      toast.error('');
    }
  };
  
  const handleShare = async () => {
    if (navigator.share) {
      try {
        await navigator.share({
          title: 'AI',
          text: '',
          url: inviteUrl
        });
      } catch (err) {
        console.log('');
      }
    } else {
      handleCopyLink();
    }
  };
  
  const handleManualCheck = () => {
    setIsChecking(true);
    checkPartnerStatus();
    setTimeout(() => setIsChecking(false), 1000);
  };
  
  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 to-purple-50 flex items-center justify-center p-4">
      <div className="max-w-2xl w-full">
        {/*  */}
        <div className="text-center mb-8">
          <div className="flex justify-center items-center mb-4">
            <Heart className="w-12 h-12 text-pink-500 animate-pulse" />
          </div>
          <h1 className="text-3xl md:text-4xl font-bold text-gray-800 mb-4">
            
          </h1>
          <p className="text-lg text-gray-600">
            
          </p>
        </div>
        
        {/*  */}
        <div className="bg-white rounded-2xl shadow-xl p-8 mb-6">
          <div className="text-center mb-6">
            <h2 className="text-2xl font-semibold text-gray-800 mb-4">
               
            </h2>
            <div className="bg-gradient-to-r from-pink-100 to-purple-100 rounded-xl p-6 mb-6">
              <p className="text-gray-600 mb-4"></p>
              <div className="text-4xl font-bold text-purple-600 font-mono tracking-wider mb-4">
                {inviteCode}
              </div>
              <p className="text-sm text-gray-500">
                
              </p>
            </div>
          </div>
          
          {/*  */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              
            </label>
            <div className="flex space-x-3">
              <input
                type="text"
                value={inviteUrl}
                readOnly
                className="flex-1 px-4 py-3 border border-gray-300 rounded-lg bg-gray-50 text-sm"
              />
              <button
                onClick={handleCopyLink}
                className={`px-6 py-3 rounded-lg font-semibold transition-all duration-300 flex items-center space-x-2 ${
                  copied 
                    ? 'bg-green-600 text-white' 
                    : 'bg-blue-600 hover:bg-blue-700 text-white'
                }`}
              >
                <Copy className="w-4 h-4" />
                <span>{copied ? '' : ''}</span>
              </button>
            </div>
          </div>
          
          {/*  */}
          <div className="flex space-x-4 mb-6">
            <button
              onClick={handleShare}
              className="flex-1 bg-gradient-to-r from-pink-600 to-purple-600 hover:from-pink-700 hover:to-purple-700 text-white font-bold py-3 px-6 rounded-xl transition-all duration-300 flex items-center justify-center space-x-2"
            >
              <Share2 className="w-5 h-5" />
              <span>TA</span>
            </button>
            
            <button
              onClick={handleManualCheck}
              disabled={isChecking}
              className="bg-gray-600 hover:bg-gray-700 text-white font-semibold py-3 px-6 rounded-xl transition-colors flex items-center space-x-2 disabled:opacity-50"
            >
              <RefreshCw className={`w-5 h-5 ${isChecking ? 'animate-spin' : ''}`} />
              <span></span>
            </button>
          </div>
          
          {/*  */}
          <div className="bg-yellow-50 border border-yellow-200 rounded-xl p-6">
            <div className="flex items-center justify-center space-x-3 mb-4">
              <Clock className="w-6 h-6 text-yellow-600" />
              <span className="text-lg font-semibold text-yellow-800">...</span>
            </div>
            <p className="text-yellow-700 text-center">
              
            </p>
            <div className="flex justify-center mt-4">
              <div className="flex space-x-1">
                <div className="w-2 h-2 bg-yellow-500 rounded-full animate-bounce"></div>
                <div className="w-2 h-2 bg-yellow-500 rounded-full animate-bounce" style={{animationDelay: '0.1s'}}></div>
                <div className="w-2 h-2 bg-yellow-500 rounded-full animate-bounce" style={{animationDelay: '0.2s'}}></div>
              </div>
            </div>
          </div>
        </div>
        
        {/*  */}
        <div className="bg-white rounded-xl shadow-lg p-6 mb-6">
          <h3 className="font-semibold text-gray-800 mb-3"> </h3>
          <ul className="text-gray-600 space-y-2 text-sm">
            <li className="flex items-start">
              <span className="w-2 h-2 bg-pink-500 rounded-full mr-3 mt-2"></span>
              
            </li>
            <li className="flex items-start">
              <span className="w-2 h-2 bg-purple-500 rounded-full mr-3 mt-2"></span>
              
            </li>
            <li className="flex items-start">
              <span className="w-2 h-2 bg-blue-500 rounded-full mr-3 mt-2"></span>
              
            </li>
            <li className="flex items-start">
              <span className="w-2 h-2 bg-green-500 rounded-full mr-3 mt-2"></span>
              ""
            </li>
          </ul>
        </div>
        
        {/*  */}
        <div className="text-center">
          <button
            onClick={resetGame}
            className="bg-gray-500 hover:bg-gray-600 text-white font-semibold py-3 px-6 rounded-lg transition-colors"
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default WaitingPage;