import React, { useState } from 'react';
import { useGame } from '../contexts/GameContext';
import { Heart, Brain, Zap, Users, User, Link } from 'lucide-react';

const IntroPage: React.FC = () => {
  const { setCurrentStep, setGameMode, startCoupleGame, joinGameByCode } = useGame();
  const [showJoinInput, setShowJoinInput] = useState(false);
  const [inviteCode, setInviteCode] = useState('');
  const [joinError, setJoinError] = useState('');

  const handleSingleTest = () => {
    setGameMode('single');
    setCurrentStep('quiz');
  };

  const handleCoupleTest = () => {
    startCoupleGame();
  };

  const handleJoinGame = () => {
    if (!inviteCode.trim()) {
      setJoinError('');
      return;
    }
    
    const success = joinGameByCode(inviteCode.trim().toUpperCase());
    if (!success) {
      setJoinError('');
    } else {
      setJoinError('');
    }
  };

  const toggleJoinInput = () => {
    setShowJoinInput(!showJoinInput);
    setJoinError('');
    setInviteCode('');
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-pink-50 flex items-center justify-center p-4">
      <div className="max-w-2xl w-full">
        {/*  */}
        <div className="text-center mb-8">
          <div className="flex justify-center items-center mb-4">
            <Brain className="w-12 h-12 text-blue-600 mr-3" />
            <Heart className="w-10 h-10 text-pink-500 mr-3" />
            <Zap className="w-12 h-12 text-blue-600" />
          </div>
          <h1 className="text-4xl md:text-5xl font-bold text-gray-800 mb-4">
            AI
          </h1>
          <p className="text-xl text-gray-600 mb-2">
            Real Couple Compatibility Analysis
          </p>
          <div className="w-24 h-1 bg-gradient-to-r from-blue-500 to-pink-500 mx-auto rounded-full"></div>
        </div>

        {/*  */}
        <div className="bg-white rounded-2xl shadow-xl p-8 mb-8">
          <div className="text-center mb-6">
            <h2 className="text-2xl font-semibold text-gray-800 mb-4">
               
            </h2>
            <p className="text-gray-600 leading-relaxed">
              
              
              
            </p>
          </div>

          <div className="grid md:grid-cols-3 gap-6 mb-8">
            <div className="text-center p-4 bg-blue-50 rounded-xl">
              <div className="text-3xl mb-2"></div>
              <h3 className="font-semibold text-gray-800 mb-2"></h3>
              <p className="text-sm text-gray-600"></p>
            </div>
            <div className="text-center p-4 bg-pink-50 rounded-xl">
              <div className="text-3xl mb-2"></div>
              <h3 className="font-semibold text-gray-800 mb-2"></h3>
              <p className="text-sm text-gray-600"></p>
            </div>
            <div className="text-center p-4 bg-purple-50 rounded-xl">
              <div className="text-3xl mb-2"></div>
              <h3 className="font-semibold text-gray-800 mb-2"></h3>
              <p className="text-sm text-gray-600"></p>
            </div>
          </div>

          <div className="bg-gradient-to-r from-blue-100 to-pink-100 rounded-xl p-6 mb-6">
            <h3 className="font-semibold text-gray-800 mb-3"> </h3>
            <ul className="text-gray-700 space-y-2">
              <li className="flex items-center">
                <span className="w-2 h-2 bg-blue-500 rounded-full mr-3"></span>
                
              </li>
              <li className="flex items-center">
                <span className="w-2 h-2 bg-pink-500 rounded-full mr-3"></span>
                
              </li>
              <li className="flex items-center">
                <span className="w-2 h-2 bg-purple-500 rounded-full mr-3"></span>
                
              </li>
              <li className="flex items-center">
                <span className="w-2 h-2 bg-indigo-500 rounded-full mr-3"></span>
                
              </li>
            </ul>
          </div>

          <div className="text-center space-y-4">
            {/*  */}
            <div className="space-y-3">
              <button
                onClick={handleCoupleTest}
                className="w-full bg-gradient-to-r from-pink-600 to-purple-600 hover:from-pink-700 hover:to-purple-700 text-white font-bold py-4 px-8 rounded-xl text-lg transition-all duration-300 transform hover:scale-105 shadow-lg flex items-center justify-center"
              >
                <Users className="w-6 h-6 mr-2" />
                 
              </button>
              
              <div className="flex items-center justify-center space-x-4">
                <div className="h-px bg-gray-300 flex-1"></div>
                <span className="text-gray-500 text-sm"></span>
                <div className="h-px bg-gray-300 flex-1"></div>
              </div>
              
              <button
                onClick={toggleJoinInput}
                className="w-full bg-gradient-to-r from-blue-600 to-teal-600 hover:from-blue-700 hover:to-teal-700 text-white font-bold py-4 px-8 rounded-xl text-lg transition-all duration-300 transform hover:scale-105 shadow-lg flex items-center justify-center"
              >
                <Link className="w-6 h-6 mr-2" />
                 
              </button>
            </div>
            
            {/*  */}
            {showJoinInput && (
              <div className="bg-gray-50 rounded-xl p-6 mt-4">
                <h3 className="font-semibold text-gray-800 mb-3"></h3>
                <div className="flex space-x-3">
                  <input
                    type="text"
                    value={inviteCode}
                    onChange={(e) => setInviteCode(e.target.value.toUpperCase())}
                    placeholder="6"
                    className="flex-1 px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-center text-lg font-mono"
                    maxLength={6}
                  />
                  <button
                    onClick={handleJoinGame}
                    className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg font-semibold transition-colors"
                  >
                    
                  </button>
                </div>
                {joinError && (
                  <p className="text-red-500 text-sm mt-2">{joinError}</p>
                )}
              </div>
            )}
            
            {/*  */}
            <div className="pt-4 border-t border-gray-200">
              <p className="text-gray-600 text-sm mb-3"></p>
              <button
                onClick={handleSingleTest}
                className="bg-gray-600 hover:bg-gray-700 text-white font-semibold py-3 px-6 rounded-lg transition-colors flex items-center justify-center mx-auto"
              >
                <User className="w-5 h-5 mr-2" />
                
              </button>
            </div>
            
            <p className="text-xs text-gray-500 mt-4">
              *
            </p>
          </div>
        </div>

        {/*  */}
        <div className="text-center text-gray-500 text-sm">
          <p> 2024 AI |     </p>
        </div>
      </div>
    </div>
  );
};

export default IntroPage;